<template>
  <div v-if="isLoading" class="loading">
    <div class="loading">
      <img
        class="loading-img"
        src="https://hbimg.huabanimg.com/1cd0c5b05f630a1cfd7a056ead30db9f0776cc9e51fe3-MpXEQu"
      />
    </div>
  </div>
  <div id="music" v-else>
    <div>
      <aplayer :audio="audio" :lrcType="3" ref="aplayer"/>
    </div>
    <table cellspacing="0">
      <thead>
        <tr>
          <th width="5%"></th>
          <th width="70%">歌曲标题</th>
          <th width="25%">歌手</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in musicList" :key="index" @click="play(item.id)">
          <td>{{index+1}}</td>
          <td>{{item.name}}</td>
          <td>{{item.ar[0].name}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import Vue from 'vue'
import APlayer from '@moefe/vue-aplayer';
import axios from 'axios'

Vue.use(APlayer, {
  defaultCover: 'https://github.com/u3u.png',
  productionTip: true,
});

export default {
  data() {
    return {
      musicList: [],
      isLoading: true,
      audio: [{
        name: '',
        artist: '',
        url: '',
        cover: '',
      }]
    };
  },
  created() {
    let obj = {
      title: "音乐",
      className: "music"
    };
    this.$emit("changeNav", obj);
    this.getData();
  },
  methods: {
    getData() {
      let url = "https://bird.ioliu.cn/netease/playlist?id=2739493093";
      axios
        .get(url)
        .then(res => {
          this.musicList = res.data.playlist.tracks;
          this.isLoading = false;
        })
        .catch(() => {
          return false;
        });
    },
    play(id){
      let url = 'https://bird.ioliu.cn/netease/song?id='+id
      axios.get(url)
      .then((res) => {
        this.audio[0].name = res.data.data.name;
        this.audio[0].artist = res.data.data.ar[0].name;
        this.audio[0].url = res.data.data.mp3.url;
        this.audio[0].cover = res.data.data.al.picUrl;
        this.$refs.aplayer.play();
      })
      .catch((error) => {
        console.log(error)
      })
    }
  }
};
</script>
<style scoped>
#music table td {
  font-size: 0.2rem;
}
#music table th {
  font-size: 0.3rem;
  color: #f37f07;
}
#music tbody tr:nth-child(odd) {
  background-color: #f3eb91ed;
}
.loading {
  width: 100vw;
  height: 83vh;
  background-color: rgba(255, 255, 255, 0.5);
  position: fixed;
  top: 1rem;
}
.loading-img {
  padding: 0;
  width: 200px;
  height: 100px;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
tbody td {
  text-align: center;
}
</style>
